body *{
	-webkit-user-select: none;
	   -moz-user-select: none;
	    -ms-user-select: none;
	        user-select: none;	/* 禁止ios和android用户选中文字 */
	-webkit-touch-callout: none;	/* 禁止ios长按时触发系统的菜单，禁止ios&android长按时下载图片 */
	-webkit-text-size-adjust: 100%;	/* 禁止横竖屏旋转后文字放大 */

	/* 取自网易严选的默认字体 */
	font-family: PingFangSC-Light,helvetica,Heiti SC;
}
a,button,input{
	-webkit-tap-highlight-color: rgba(0,0,0,0);	
  /* 去除android下a/button/input标签被点击时产生的边框 
  & 去除ios下a标签被点击时产生的半透明灰色背景 */
}
button,input{
	-webkit-appearance: none;	/* ios下button与input都会有个默认背景 */
	border-radius: 0;	/* ios下input有个默认圆角 */
}
/* 根节点下定义一个变量 */
:root{
	--c1:#dd1a21;
}
body{
	margin: 0;
	font-size: 12px;
	background-color: #eee;
	color: #333;
}
a {
	text-decoration: none;
	color: #333;
}
input {
	outline: none;
	vertical-align: middle;
	padding: 0;
}
ul{
	margin: 0;
	padding: 0;
	list-style: none;
}
img{
	vertical-align: middle;
}
h1,h2,h3,h4,p{
	margin: 0;
}
html{
	font-size: 26.666666666vw;
}
.clearfix:after{/*伪元素是行内元素 正常浏览器清除浮动方法*/
        content: "";
        display: block;
        height: 0;
        clear:both;
        visibility: hidden;
}
.clearfix{
	*zoom: 1;/*ie6清除浮动的方式 *号只有IE6-IE7执行，其他浏览器不执行*/
}
/* 使用字体图标 */
@font-face {
	font-family: 'iconfont';
  src: url(data:application/vnd.ms-fontobject;base64,);
	src: url(data:application/vnd.ms-fontobject;base64,?#iefix) format('embedded-opentype'),
		url(data:application/font-woff2;base64,) format('woff2'),
		url(./static/fonts/iconfont.6a5d14d.woff) format('woff'),
		url(./static/fonts/iconfont.972de57.ttf) format('truetype'),
		url(./static/img/iconfont.f1de83e.svg#iconfont) format('svg');
}
.iconfont {
	font-family: "iconfont" !important;
	font-size: 16px;
	font-style: normal;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}
.icon {
  width: 1em;
  height: 1em;
  vertical-align: -0.15em;
  fill: currentColor;
  overflow: hidden;
}
